class NgxBootstrapIconsModule {
    constructor(_icons) {
        this._icons = _icons;
        if (!this._icons)
            throw new Error('No icon provided. Make sure to use \'NgxBootstrapIconsModule.pick({ ... })\' when importing the module\n');
    }
    static pick(icons) {
        return {
            ngModule: NgxBootstrapIconsModule,
            providers: [
                { provide: Icons, multi: true, useValue: icons },
            ],
        };
    }
}
NgxBootstrapIconsModule.decorators = [
    { type: NgModule, args: [{
                declarations: [NgxBootstrapIconsLibComponent],
                exports: [NgxBootstrapIconsLibComponent],
            },] }
];
NgxBootstrapIconsModule.ctorParameters = () => [
    { type: Icons, decorators: [{ type: Optional }] }
];

